.layout {
  background-color : #f9f9f9;
  height : 100%;
  display : flex;
  justify-content: center;
  padding-top: 72px;
  box-sizing : border-box;
}

.container {
  flex : 1;
  max-width: 1284px;
  margin-top: -55px;
}

/* flex grow : 0, flex-shrink : 1, flex-basis : 214px
   flex 값은 flex-grow, flex-shirink, flex-basis 순서로 결정이 된다 */

/* 메뉴의 너비가 고정 일 경우의 반응형 해상도 */

@media screen and (max-width: 529px) {
  .container {
    flex: 0 1 214px;
  }
}
@media screen and (min-width: 530px) and (max-width: 743px) {
  .container {
    flex: 0 1 428px;
  }
}
@media screen and (min-width: 744px) and (max-width: 1125px) {
  .container {
    flex: 0 1 642px;
    /* 메뉴의 너비가 72px (반응형), 영상의 너비가 642px, 좌우여백인 30px인 총 합 744px보다 작을 경우, 영상 크기를 한 단계 작게 설정 */
  }
}
@media screen and (min-width: 1126px) and (max-width: 1339px) {
  .container {
    flex: 0 1 856px;
  }
}
@media screen and (min-width: 1340px) and (max-width: 1553px) {
  .container {
    flex: 0 1 1070px;
  }
}
@media screen and (min-width: 1554) {
  .container {
    flex: 0 1 1284px;
  }
}

/* 페이지 너비가 '영상너비 + 메뉴너비 + 30(좌우여백)보다 작아지면, 영상 크기를 한 단계 작게 설정' */
